import { useState } from "react";

import BootstrapTable from "react-bootstrap-table-next";

import products from "./products";

import "bootstrap/dist/css/bootstrap.min.css";
import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
import "./App.css";

const columns = [
  {
    dataField: "id",
    text: "Segment ID",
    style: { color: "#5B6EFD" },
  },
  {
    dataField: "name",
    text: "Segment Name",
  },
  {
    dataField: "type",
    text: "Segment Type",
  },
  {
    dataField: "industry",
    text: "Industry",
  },
  {
    dataField: "audienceSize",
    text: "Audience Size",
  },
];

const rowStyle = { fontWeight: "bold" };

const App = () => {
  const [selected, setSelected] = useState([1, 2]);

  const handleOnSelect = (row, isSelect) => {
    if (isSelect) {
      setSelected((prevState) => [...prevState, row.id]);
    } else {
      setSelected((prevState) => prevState.filter((id) => id !== row.id));
    }
  };

  const handleOnSelectAll = (isSelect, rows) => {
    const ids = rows.map((r) => r.id);
    if (isSelect) {
      setSelected(ids);
    } else {
      setSelected([]);
    }
  };

  const handleOnClickConfirm = () => {
    console.log(selected);
  };

  const selectRow = {
    mode: "checkbox",
    clickToSelect: true,
    selected: selected,
    onSelect: handleOnSelect,
    onSelectAll: handleOnSelectAll,
  };

  return (
    <div>
      <BootstrapTable
        keyField="id"
        data={products}
        columns={columns}
        selectRow={selectRow}
        rowStyle={rowStyle}
        // bordered={false}
        wrapperClasses="table-wrapper"
        headerClasses="text-primary"
      />
      <button
        className="btn btn-lg btn-primary rounded-pill confirm-btn"
        onClick={handleOnClickConfirm}
      >
        Confirm
      </button>
    </div>
  );
};

export default App;
